import { createStore } from "vuex";

export default createStore({
  state: {
    arrayItems: [],
    arrayItemsAll: [],
    arrayInfo: [],
    arrayFiltros: {
      ceco: [],
      departamento: [],
      vidaUtil: [],
      añoInstalacion: [],
      inversion: [],
      arrayProyecciones: [],
    },

  },

  mutations: {
    setItems(state, payload) {
      state.arrayItems = payload;
    },

    setInfo(state, payload) {
      state.arrayInfo = payload;
    },

    setItemsFiltrados(state, payload) {
      state.arrayItems = payload;
    },

    setFiltros(state, payload) {
      state.arrayFiltros = payload
    },

    setArrayProyecciones(state, payload) {
      state.arrayProyecciones = payload
    },

  },

  actions: {

    async fetchItems({ commit }, array) {
      try {
        const res = await fetch(`../apis/proyecciones.php?action=obtenerItems5&idDestino=${array.idDestino}&idUsuario=${array.idUsuario}`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        commit("setItems", data);
      } catch (error) {
        console.log(error);
      }
    },

    async getProyecciones({ commit }, array) {
      try {
        const res = await fetch(`../apis/proyecciones.php?action=obtenerProyeccionItems5&idDestino=${array.idDestino}&idUsuario=${array.idUsuario}`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        commit("setArrayProyecciones", data);
      } catch (error) {
        console.log(error);
      }
    },

    async fetchItemsFiltrados({ commit, state }, array) {

      const filtro = await state.arrayItemsAll.filter(item => {
        if (
          (item.departamento.toUpperCase() == array.departamento.toUpperCase() || array.departamento.toUpperCase() == "TODOS") &&
          (item.seccion.toUpperCase() == array.ceco.toUpperCase() || array.ceco.toUpperCase() == "TODOS")
        ) {

          if (item.items.length) {
            item.items.forEach(element => {
              if (element.itemsNivel3) {
                element.itemsNivel3.filter((item, x) => {
                  if (item.vidaUtil == array.vidaUtil || array.vidaUtil.toUpperCase() == "TODOS") {
                    return true;
                  }
                  return false;
                })
              }
            });
          }
        }
        return false;
      });

      await commit('setItemsFiltrados', filtro);
    },

    async fetchInfo({ commit }, array) {
      try {
        const res = await fetch(`../apis/proyecciones.php?action=obtenerInfoDestino&idDestino=${array.idDestino}&idUsuario=${array.idUsuario}`);
        const data = await res.json();
        commit("setInfo", data);
      } catch (error) {
        console.log(error);
      }
    },

    getFiltros({ commit, state }) {
      const ceco = [];
      const departamento = [];
      const vidaUtil = [];
      const añoInstalacion = [];
      const inversion = [];
      const data = {
        ceco: [],
        departamento: [],
        vidaUtil: [],
        añoInstalacion: [],
        inversion: []
      };

      state.arrayItems.forEach(function (item) {

        // CECO
        if (!ceco.includes(item.seccion)) {
          ceco.push(item.seccion)
        }

        // DEPARTAMENTO
        if (!departamento.includes(item.departamento)) {
          departamento.push(item.departamento)
        }

        if (item.items) {
          item.items.forEach(item => {
            if (item.itemsNivel3) {
              item.itemsNivel3.forEach(item => {

                // VIDA UTIL
                if (!vidaUtil.includes(item.vidaUtil)) {
                  vidaUtil.push(item.vidaUtil)
                }

                // VIDA UTIL
                if (!añoInstalacion.includes(item.añoInstalacion)) {
                  añoInstalacion.push(item.añoInstalacion)
                }

                // VIDA UTIL
                if (!inversion.includes(item.inversion)) {
                  inversion.push(item.inversion)
                }
              })
            }
          })
        }
      })

      // ORDENA RESULTADOS DE ARRAY
      ceco.sort();
      departamento.sort();
      vidaUtil.sort();
      añoInstalacion.sort();
      inversion.sort();



      commit('setFiltros', {
        ceco: ceco,
        departamento: departamento,
        vidaUtil: vidaUtil,
        añoInstalacion: añoInstalacion,
        inversion: inversion
      });
    },

  },

  modules: {},
});
