<template>
  <tr v-if="toggle1 && toggle2" class="tr">
    <td colspan="10">
      <table class="w-full text-xs table-fixed">
        <tr class="cursor-pointer font-bold">
          <td class="w-1/10 bg-green-100 td" @click="toggle = !toggle">
            <!-- TOGGLE : FALSE -->
            <svg
              class="w-3 h-3 ml-3"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
              v-if="array.itemsNivel3.length && toggle"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M19 9l-7 7-7-7"
              ></path>
            </svg>

            <!-- TOGGLE : TRUE -->
            <svg
              class="w-3 h-3 ml-3"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
              v-if="array.itemsNivel3.length && !toggle"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M9 5l7 7-7 7"
              ></path>
            </svg>
          </td>

          <td
            colspan="2"
            class="w-1/10 bg-green-100 text-right uppercase td"
            data-label="TITULO"
            @click="toggle = !toggle"
          >
            <div v-if="edicion" class="mr-10 w- truncate">
              {{ array.titulo }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-1
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Titulo"
                v-model="data.titulo"
              />
            </div>
          </td>
          <td class="w-1/10 bg-green-100 td" @click="toggle = !toggle">
            <div v-if="edicion" class="mr-10 w- truncate">
              {{ array.vidaUtil > 0 ? array.vidaUtil + " años" : "" }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-1
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Años Vida Util"
                v-model="data.vidaUtil"
              />
            </div>
          </td>
          <td class="w-1/10 bg-green-100 td" @click="toggle = !toggle">
            <div v-if="edicion" class="mr-10 w- truncate">
              {{ array.añoInstalacion > 0 ? array.añoInstalacion : "" }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-1
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Año Instalación"
                v-model="data.añoInstalacion"
              />
            </div>
          </td>
          <td class="w-1/10 bg-green-100 td" @click="toggle = !toggle">
            <div v-if="edicion" class="mr-10 w- truncate">
              {{ array.inversion.length > 0 ? array.inversion : "" }}
            </div>
            <div
              class="
                flex flex-row
                items-center
                justify-between
                py-0
                max-w-xs
                w-full
                mx-auto
              "
              v-if="!edicion"
            >
              <div>
                <input
                  class="form-radio"
                  type="radio"
                  value="OS&E"
                  v-model="data.inversion"
                />
                <label
                  class="text-green-500 font-bold ml-1 text-sm"
                  :for="['opt_1_' + array.idItem]"
                  >OS&E</label
                >
              </div>
              <div>
                <input type="radio" value="FF&E" v-model="data.inversion" />
                <label
                  class="text-green-500 font-bold ml-1 text-sm"
                  :for="['opt_2_' + array.idItem]"
                  >FF&E</label
                >
              </div>
            </div>
          </td>
          <td class="w-1/10 bg-green-100 td" @click="toggle = !toggle">
            <div v-if="edicion" class="mr-10 w- truncate">
              {{
                array.coste > 0
                  ? new Intl.NumberFormat().format(array.coste)
                  : ""
              }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-1
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Coste"
                v-model="data.coste"
              />
            </div>
          </td>
          <td class="w-1/10 bg-green-100 td" @click="toggle = !toggle">
            <div v-if="edicion" class="mr-10 w- truncate">
              {{ array.unidades > 0 ? "$ " + array.unidades : "" }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-1
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Unidades"
                v-model="data.unidades"
              />
            </div>
          </td>
          <td class="w-1/10 bg-green-100 td" @click="toggle = !toggle">
            $ {{ new Intl.NumberFormat().format(array.total) }}
          </td>
          <td class="w-1/10 bg-green-100 td">
            <div class="flex flex-row justify-evenly" v-if="edicion">
              <button
                class="
                  bg-blue-200
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="agregarNivel3(array)"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M12 6v6m0 0v6m0-6h6m-6 0H6"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-yellow-200
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="
                  edicion = !edicion;
                  editarItem(array);
                "
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-red-200
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="confirm('eliminarNivel2', array)"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                  ></path>
                </svg>
              </button>
            </div>

            <div class="flex flex-row justify-evenly" v-if="!edicion">
              <div class="w-5"></div>

              <button
                class="
                  bg-green-400
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="
                  edicion = !edicion;
                  confirm('editarNivel3', data);
                "
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                    clip-rule="evenodd"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-gray-400
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="edicion = !edicion"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    d="M7.707 3.293a1 1 0 010 1.414L5.414 7H11a7 7 0 017 7v2a1 1 0 11-2 0v-2a5 5 0 00-5-5H5.414l2.293 2.293a1 1 0 11-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                    clip-rule="evenodd"
                  ></path>
                </svg>
              </button>
            </div>
          </td>
        </tr>
        <TablaNivel4
          v-for="(x, y) in array.itemsNivel3"
          :key="y"
          :array="x"
          :toggle1="toggle1"
          :toggle2="toggle2"
          :toggle3="toggle"
        />
      </table>
    </td>
  </tr>
</template>

<script>
import { useStore } from "vuex";
import { ref } from "vue";
import TablaNivel4 from "../components/TablaNivel4.vue";
export default {
  name: "TablaNivel3",
  components: { TablaNivel4 },
  props: {
    array: Object,
    toggle1: Boolean,
    toggle2: Boolean,
  },

  setup(props) {
    const store = useStore();
    const Swal = require("sweetalert2");

    const edicion = ref(true);
    const toggle = ref(false);

    const data = ref({
      idItem: 0,
      idEquipo: 0,
      titulo: "",
      vidaUtil: "",
      añoInstalacion: "",
      inversion: "",
      coste: "",
      unidades: "",
      total: "",
    });

    //   ELIMINA ITEMS NIVEL 3
    const eliminarNivel3 = async (array) => {
      const idUsuario = localStorage.getItem("usuario");
      const idDestino = localStorage.getItem("idDestino");
      const action = "eliminarNivel3";
      const url = `../apis/proyecciones.php?action=${action}&idDestino=${idDestino}&idUsuario=${idUsuario}&idItem=${array.idItem}`;

      const peticion = await fetch(url);
      const resp = await peticion.json();

      if (resp == 1) {
        alertify.success("Item Eliminado");
        store.dispatch("fetchItems", {
          idDestino: idDestino,
          idUsuario: idUsuario,
          ceco: "TODOS",
          departamento: "TODOS",
          vidaUtil: "TODOS",
          añoInstalacion: "TODOS",
          inversion: "TODOS",
          palabra: "",
        });
      } else {
        alertify.message("Intente de Nuevo");
      }
    };

    const editarNivel3 = async (array) => {
      try {
        const idUsuario = localStorage.getItem("usuario");
        const idDestino = localStorage.getItem("idDestino");

        const action = "actualizar";
        const url = `../apis/proyecciones.php?action=${action}&idDestino=${idDestino}&idUsuario=${idUsuario}&idItem=${array.idItem}`;

        if (
          data.value.titulo.length <= 0 ||
          data.value.vidaUtil < 0 ||
          data.value.añoInstalacion.length < 0 ||
          data.value.inversion.length < 0 ||
          data.value.coste < 0 ||
          data.value.unidades < 0
        ) {
          alertify.warning("Datos NO Validos, Intente de Nuevo");
          return;
        }

        if (
          data.value.titulo == array.titulo &&
          data.value.vidaUtil == array.vidaUtil &&
          data.value.añoInstalacion == array.añoInstalacio &&
          data.value.inversion == array.inversion &&
          data.value.coste == array.coste &&
          data.value.unidades == array.unidades
        ) {
          alertify.message("Sin Cambios");
          return;
        }

        const peticion = await fetch(url, {
          method: "POST",
          body: JSON.stringify(data.value),
        });
        const resp = await peticion.json();

        if (resp == 1) {
          alertify.success("Datos Actualizados");
          store.dispatch("fetchItems", {
            idDestino: idDestino,
            idUsuario: idUsuario,
            ceco: "TODOS",
            departamento: "TODOS",
            vidaUtil: "TODOS",
            añoInstalacion: "TODOS",
            inversion: "TODOS",
            palabra: "",
          });
        } else {
          alertify.message("Intente de Nuevo");
        }
      } catch (error) {
        console.log(error);
      }
    };

    const editarItem = (array) => {
      data.value.idItem = array.idItem;
      data.value.idEquipo = array.idEquipo;
      data.value.titulo = array.titulo;
      data.value.vidaUtil = array.vidaUtil;
      data.value.añoInstalacion = array.añoInstalacion;
      data.value.inversion = array.inversion;
      data.value.coste = array.coste;
      data.value.unidades = array.unidades;
      data.value.total = array.total;
    };

    const agregarNivel3 = async (array) => {
      const { value: formValues } = await Swal.fire({
        title: "¿AGREGAR ACTIVO?",
        confirmButtonText: "Confirmar",
        showCancelButton: true,
        html: `
        
        <div class="mb-5 flex flex-col">
          <label class="font-bold text-xl">Activo: </label>
          <input id="swal-input1" class="swal2-input" placeholder="Nombre del Activo">
        </div>
        <div class="mb-5 flex flex-col">
          <label class="font-bold text-xl">Vida Útil (Años): </label>
          <input id="swal-input2" class="swal2-input" placeholder="Valor Numérico: 3">
        </div>
        <div class="mb-5 flex flex-col">
          <label class="font-bold text-xl">Año Instalación: </label>
            <input id="swal-input3" class="swal2-input" placeholder="Valor Numérico: 2021">
        </div>
        <div class="mb-5 flex flex-col swal2-selec">
          <label class="font-bold text-xl">Inversón: </label>
          <select id="swal-input4" class="swal2-input">
            <option value="FF&E">FF&E</option>
            <option value="OS&E">OS&E</option>
          </select>
        </div>
        <div class="mb-5 flex flex-col">
          <label class="font-bold text-xl">Coste: </label>
          <div class="flex flex-row items-center swal2-input">
          $ <input id="swal-input5" class="ml-2 py-2 w-full focus:ring ring-gray-#e5e7eb" placeholder="Valor Numérico: 100.56">
          </div>
        </div>
        <div class="mb-5 flex flex-col">
          <label class="font-bold text-xl">Unidades: </label>
          <input id="swal-input6" class="swal2-input" placeholder="Valor Numérico: 2">
        </div>
      `,
        focusConfirm: false,
        preConfirm: () => {
          return [
            document.getElementById("swal-input1").value,
            document.getElementById("swal-input2").value,
            document.getElementById("swal-input3").value,
            document.getElementById("swal-input4").value,
            document.getElementById("swal-input5").value,
            document.getElementById("swal-input6").value,
          ];
        },
      });

      if (formValues) {
        if (
          formValues[0].length <= 0 &&
          formValues[1] >= 0 &&
          formValues[2] >= 0 &&
          formValues[4] >= 0 &&
          formValues[5] >= 0
        ) {
          alertify.message("Datos NO Validos");
        } else {
          const idUsuario = localStorage.getItem("usuario");
          const idDestino = localStorage.getItem("idDestino");
          const action = "agregarNivel3";
          const url = `../apis/proyecciones.php?action=${action}&idDestino=${idDestino}&idUsuario=${idUsuario}&idDepartamento=${array.idDepartamento}&idNivel=${array.idItem}`;
          const peticion = await fetch(url, {
            method: "POST",
            body: JSON.stringify({
              titulo: formValues[0],
              vidaUtil: formValues[1],
              añoInstalacion: formValues[2],
              inversion: formValues[3],
              coste: formValues[4],
              unidades: formValues[5],
            }),
          });
          const resp = await peticion.json();
          if (resp == 1) {
            alertify.success("Item Agregado");
            store.dispatch("fetchItems", {
              idDestino: idDestino,
              idUsuario: idUsuario,
              ceco: "TODOS",
              departamento: "TODOS",
              vidaUtil: "TODOS",
              añoInstalacion: "TODOS",
              inversion: "TODOS",
              palabra: "",
            });
          } else {
            alertify.message("Intente de Nuevo");
          }
        }
      }
    };

    const confirm = (opcion, array) => {
      const msj =
        opcion == "eliminarDepartamento"
          ? "ADVERTENCIA: Se Eliminará el Registro y sus Componentes"
          : opcion == "eliminarNivel2"
          ? "ADVERTENCIA: Se Eliminará el Registro y sus Componentes"
          : opcion == "agregarDepartamento"
          ? "INFORMACIÓN: Se agregara un departamento Vacio."
          : opcion == "agregarNivel2"
          ? "INFORMACIÓN: Se agregara Item Nivel 2."
          : opcion == "agregarNivel3"
          ? "INFORMACIÓN: Se agregara Item Nivel 2"
          : opcion == "editarDepartamento"
          ? "INFORMACIÓN: Se Actualizarán los Datos."
          : opcion == "editarNivel2"
          ? "INFORMACIÓN: Se Actualizarán los Datos."
          : opcion == "editarNivel3"
          ? "INFORMACIÓN: Se Actualizarán los Datos."
          : "INFORMACIÓN: --";

      alertify.confirm(
        "¿Aplicar Acción?",
        msj,
        () => {
          // OPCION PARA AGREGAR NIVEL 3
          if (opcion == "eliminarNivel3") {
            eliminarNivel3(array);
            return;
          }

          // OPCION PARA AGREGAR NIVEL 3
          if (opcion == "editarNivel3") {
            editarNivel3(array);
            return;
          }
        },
        () => {
          alertify.error("Proceso Cancelado");
        }
      );
    };

    return {
      confirm,
      edicion,
      data,
      toggle,
      editarItem,
      agregarNivel3,
    };
  },
};
</script>

<style scoped>
.tr {
  border: 1px solid rgb(255, 255, 255);
  padding: -0.35em;
}

.td {
  padding: 1em;
}
</style>
