<template>
  <tr v-if="toggle1 && toggle2 && toggle3 && toggle4 && toggle5" class="tr">
    <td colspan="10">
      <table class="w-full table-fixed">
        <tr class="cursor-pointer">
          <td class="w-1/10 h-12 bg-gray-50 td"></td>
          <td class="w-1/10 bg-gray-50 td">ACTIVO</td>
          <td
            class="w-1/10 bg-gray-50 border-r-2 border-l-2 border-green-100 td"
          >
            <div v-if="edicion">
              {{ array.titulo }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-2
                  px-2
                  bg-gray-50
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                v-model="data.titulo"
              />
            </div>
          </td>
          <td
            class="w-1/10 bg-gray-50 border-r-2 border-l-2 border-green-100 td"
            data-label="VIDA ÚTIL"
          >
            <div v-if="edicion">
              {{
                array.vidaUtil == 1
                  ? array.vidaUtil + " año"
                  : array.vidaUtil > 1
                  ? array.vidaUtil + " años"
                  : 0 + " años"
              }}
            </div>
            <div
              class="py-0 max-w-xs w-full mx-auto flex flex-row items-center"
              v-if="!edicion"
            >
              <input
                class="
                  py-2
                  px-2
                  mx-3
                  bg-gray-50
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Años Vida Util"
                v-model="data.vidaUtil"
              />
              años
            </div>
          </td>
          <td
            class="w-1/10 bg-gray-50 border-r-2 border-l-2 border-green-100 td"
            data-label="AÑO INSTALACIÓN"
          >
            <div v-if="edicion">
              {{ array.añoInstalacion }}
            </div>
            <div class="max-w-xs w-full mx-auto" v-if="!edicion">
              <select
                class="font-bold text-lg text-green-500"
                v-model="data.añoInstalacion"
                :value="array.añoInstalacion"
              >
                <option disabled>Seleccione</option>
                <option v-for="year in 31" :key="year" :value="2000 + year">
                  {{ 2000 + year }}
                </option>
              </select>
            </div>
          </td>
          <td
            class="w-1/10 bg-gray-50 border-r-2 border-l-2 border-green-100 td"
            data-label="INVERSIÓN"
          >
            <div v-if="edicion">
              {{ array.inversion }}
            </div>
            <div
              class="
                py-0
                max-w-xs
                w-full
                mx-auto
                flex flex-row
                items-center
                justify-between
              "
              v-if="!edicion"
            >
              <div>
                <input
                  class="form-radio"
                  type="radio"
                  :id="['opt_1_' + array.idItem]"
                  value="OS&E"
                  v-model="data.inversion"
                />
                <label
                  class="text-green-500 font-bold ml-1 text-sm"
                  :for="['opt_1_' + array.idItem]"
                  >OS&E</label
                >
              </div>
              <div>
                <input
                  type="radio"
                  :id="['opt_2_' + array.idItem]"
                  value="FF&E"
                  v-model="data.inversion"
                />
                <label
                  class="text-green-500 font-bold ml-1 text-sm"
                  :for="['opt_2_' + array.idItem]"
                  >FF&E</label
                >
              </div>
            </div>
          </td>
          <td
            class="w-1/10 bg-gray-50 border-r-2 border-l-2 border-green-100 td"
            data-label="COSTE"
          >
            <div v-if="edicion">
              $ {{ new Intl.NumberFormat().format(array.coste) }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-2
                  px-2
                  bg-gray-50
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Coste"
                v-model="data.coste"
              />
            </div>
          </td>
          <td
            class="w-1/10 bg-gray-50 border-r-2 border-l-2 border-green-100 td"
            data-label="UNIDADES"
          >
            <div v-if="edicion">
              {{ array.unidades > 0 ? array.unidades : "-" }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-2
                  px-2
                  bg-gray-50
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                :placeholder="array.unidades + ' Unidades'"
                v-model="data.unidades"
              />
            </div>
          </td>
          <td
            class="w-1/10 bg-gray-50 border-l-2 border-green-100 td"
            data-label="TOTAL"
          >
            $
            {{
              array.total > 0 ? new Intl.NumberFormat().format(array.total) : 0
            }}
          </td>
          <td class="w-1/10 bg-gray-50 border-green-100 td">
            <div class="flex flex-row justify-evenly" v-if="edicion">
              <div class="w-5 h-5"></div>

              <button
                class="
                  bg-yellow-200
                  py-2
                  px-2
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="
                  edicion = !edicion;
                  editarItem(array);
                "
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-red-200
                  py-2
                  px-2
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="confirm('eliminarItem', array)"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                  ></path>
                </svg>
              </button>
            </div>

            <div class="flex flex-row justify-evenly" v-if="!edicion">
              <div class="w-5 h-5"></div>

              <button
                class="
                  bg-green-400
                  py-1
                  px-2
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="
                  edicion = !edicion;
                  confirm('actualizar', data);
                "
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                    clip-rule="evenodd"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-gray-400
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="edicion = !edicion"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    d="M7.707 3.293a1 1 0 010 1.414L5.414 7H11a7 7 0 017 7v2a1 1 0 11-2 0v-2a5 5 0 00-5-5H5.414l2.293 2.293a1 1 0 11-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                    clip-rule="evenodd"
                  ></path>
                </svg>
              </button>
            </div>
          </td>
        </tr>
      </table>
    </td>
  </tr>
</template>

<script>
import { useStore } from "vuex";
import { ref } from "vue";
export default {
  name: "TablaNivel6",
  props: {
    array: Object,
    toggle1: Boolean,
    toggle2: Boolean,
    toggle3: Boolean,
    toggle4: Boolean,
    toggle5: Boolean,
  },

  setup() {
    const store = useStore();

    const data = ref({
      idItem: 0,
      idEquipo: 0,
      titulo: "",
      vidaUtil: "",
      añoInstalacion: "",
      inversion: "",
      coste: "",
      unidades: "",
      total: "",
    });

    const edicion = ref(true);

    // MODO EDICION
    const editarItem = (array) => {
      data.value.idItem = array.idItem;
      (data.value.idEquipo = 0), (data.value.titulo = array.titulo);
      data.value.vidaUtil = array.vidaUtil;
      data.value.añoInstalacion = array.añoInstalacion;
      data.value.inversion = array.inversion;
      data.value.coste = array.coste;
      data.value.unidades = array.unidades;
      data.value.total = array.total;
    };

    // CONFIRMACIONES PARA OPCIONES
    const confirm = (opcion, array) => {
      const msj =
        opcion == "eliminarItem"
          ? "ADVERTENCIA: Se Eliminará el Registro y sus Componentes (" +
            array.titulo +
            ")"
          : opcion == "actualizar"
          ? "INFORMACIÓN: Se Actualizarán los Datos de " + array.titulo
          : "INFORMACIÓN: ";

      alertify.confirm(
        "¿Aplicar Acción?",
        msj,
        () => {
          // OPCION PARA AGREGAR NIVEL 3
          if (opcion == "eliminarItem") {
            eliminarItem(array);
            return;
          }

          // OPCION PARA AGREGAR NIVEL 3
          if (opcion == "actualizar") {
            actualizar(array);
            return;
          }
        },
        () => {
          alertify.error("Proceso Cancelado");
        }
      );
    };

    // ACTUALIZA INFORMACION DEL ITEM
    const actualizar = async (array) => {
      try {
        const idUsuario = localStorage.getItem("usuario");
        const idDestino = localStorage.getItem("idDestino");

        const url = `../apis/proyecciones.php?action=actualizar&idDestino=${idDestino}&idUsuario=${idUsuario}`;

        if (
          array.titulo.length <= 0 ||
          array.vidaUtil < 0 ||
          array.añoInstalacion.length < 0 ||
          array.inversion.length < 0 ||
          array.coste < 0 ||
          array.unidades < 0
        ) {
          alertify.warning("Datos NO Validos, Intente de Nuevo");
          return;
        }

        const peticion = await fetch(url, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const resp = await peticion.json();
        if (resp == 1) {
          alertify.success("Datos Actualizados");
          store.dispatch("fetchItems", {
            idDestino: idDestino,
            idUsuario: idUsuario,
            ceco: "TODOS",
            departamento: "TODOS",
            vidaUtil: "TODOS",
            añoInstalacion: "TODOS",
            inversion: "TODOS",
            palabra: "",
          });
        } else {
          alertify.message("Intente de Nuevo");
        }
      } catch (error) {
        console.log(error);
      }
    };

    // ELIMINA ITEM
    const eliminarItem = async (array) => {
      try {
        const idUsuario = localStorage.getItem("usuario");
        const idDestino = localStorage.getItem("idDestino");

        const url = `../apis/proyecciones.php?action=eliminarItem&idDestino=${idDestino}&idUsuario=${idUsuario}`;

        const peticion = await fetch(url, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const resp = await peticion.json();
        if (resp == 1) {
          alertify.success("Item Eliminado");
          store.dispatch("fetchItems", {
            idDestino: idDestino,
            idUsuario: idUsuario,
            ceco: "TODOS",
            departamento: "TODOS",
            vidaUtil: "TODOS",
            añoInstalacion: "TODOS",
            inversion: "TODOS",
            palabra: "",
          });
        } else {
          alertify.message("Intente de Nuevo");
        }
      } catch (error) {
        console.log(error);
      }
    };

    return { edicion, data, editarItem, confirm };
  },
};
</script>

<style scope>
.positive {
  color: white;
  font-size: 1.2rem;
  /* background-color: #5bbd72 !important; */
  background-color: rgba(52, 211, 153);
  border-radius: 0.45rem;
}

.black {
  color: white;
  font-size: 1.2rem;
  border-radius: 0.45rem;
  /* background-color: #5c6166; */
  background-color: rgba(55, 65, 81);
}
</style>