<template>
  <tr class="cursor-pointer">
    <td colspan="10">
      <table class="w-full text-xs table-fixed">
        <tr class="cursor-pointer tr">
          <td
            data-label="DEPTO"
            class="w-1/10 bg-green-200 text-green-700 font-bold uppercase td"
            @click="toggle1 = !toggle1"
          >
            <div v-if="edicion">
              {{ array.departamento }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-1
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Departamento"
                v-model="departamento"
              />
            </div>
          </td>
          <td
            data-label="CECO"
            class="w-1/10 bg-green-200 text-green-700 font-bold uppercase td"
            @click="toggle1 = !toggle1"
          >
            <div v-if="edicion">
              {{ array.seccion }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion">
              <input
                class="
                  py-1
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="CECO/SECCIÓN"
                v-model="seccion"
              />
            </div>
          </td>
          <td class="w-1/10 bg-green-200 td" @click="toggle1 = !toggle1"></td>
          <td class="w-1/10 bg-green-200 td" @click="toggle1 = !toggle1"></td>
          <td class="w-1/10 bg-green-200 td" @click="toggle1 = !toggle1"></td>
          <td class="w-1/10 bg-green-200 td" @click="toggle1 = !toggle1"></td>
          <td class="w-1/10 bg-green-200 td" @click="toggle1 = !toggle1"></td>
          <td class="w-1/10 bg-green-200 td" @click="toggle1 = !toggle1"></td>
          <td
            data-label="TOTAL"
            class="w-1/10 bg-green-200 text-green-700 font-bold td"
            @click="toggle1 = !toggle1"
          >
            $
            {{
              array.total > 0 ? new Intl.NumberFormat().format(array.total) : 0
            }}
          </td>
          <td class="w-1/10 bg-green-200 border-green-100 td">
            <div class="flex flex-row justify-evenly" v-if="edicion">
              <button
                class="
                  bg-blue-200
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="agregarNivel2(array)"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M12 6v6m0 0v6m0-6h6m-6 0H6"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-yellow-200
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="
                  edicion = !edicion;
                  editar(array);
                "
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-red-200
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="confirm('eliminarDepartamento', array)"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                  ></path>
                </svg>
              </button>
            </div>

            <div class="flex flex-row justify-evenly" v-if="!edicion">
              <div class="w-5"></div>

              <button
                class="
                  bg-green-400
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="
                  edicion = !edicion;
                  confirm('editarDepartamento', array);
                "
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                    clip-rule="evenodd"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-gray-400
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="edicion = !edicion"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    d="M7.707 3.293a1 1 0 010 1.414L5.414 7H11a7 7 0 017 7v2a1 1 0 11-2 0v-2a5 5 0 00-5-5H5.414l2.293 2.293a1 1 0 11-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                    clip-rule="evenodd"
                  ></path>
                </svg>
              </button>
            </div>
          </td>
        </tr>
      </table>
      <Trnivel2
        class="w-full text-xs table-fixed"
        v-for="(c, d) in array.items"
        :key="d"
        :array="[c]"
        :toggle1="toggle1"
      >
      </Trnivel2>
    </td>
  </tr>
</template>

<script>
import { ref } from "vue";
import { useStore } from "vuex";
import Trnivel2 from "@/components/Trnivel2.vue";

export default {
  name: "TablaIdentada",
  components: { Trnivel2 },
  props: {
    array: Object,
    toggle1: { type: Boolean },
  },
  
  setup(props) {
    const store = useStore();
    const Swal = require("sweetalert2");

    const edicion = ref(true);
    const departamento = ref("");
    const seccion = ref("");

    const agregarNivel2 = async (array) => {
      const { value: formValues } = await Swal.fire({
        title: "AGREGAR SUBSECCIÓN?",
        showCancelButton: true,
        html: `
        <div class="flex flex-col">
          <label class="font-bold text-xl">Subsección: </label>
          <input id="swal-input1" class="swal2-input">
        </div>
      `,
        focusConfirm: false,
        preConfirm: () => {
          return [document.getElementById("swal-input1").value];
        },
      });

      if (formValues) {
        if (formValues[0].length <= 0) {
          alertify.message("Datos NO Validos");
          return;
        }

        const idUsuario = localStorage.getItem("usuario");
        const idDestino = localStorage.getItem("idDestino");
        const action = "agregarNivel2";
        const url = `../apis/proyecciones.php?action=${action}&idDestino=${idDestino}&idUsuario=${idUsuario}&idDepartamento=${array.idDepartamento}`;

        const peticion = await fetch(url, {
          method: "POST",
          body: JSON.stringify({
            titulo: formValues[0],
          }),
        });
        const resp = await peticion.json();
        if (resp == 1) {
          alertify.success("Item Agregado");
          store.dispatch("fetchItems", {
            idDestino: idDestino,
            idUsuario: idUsuario,
            ceco: "TODOS",
            departamento: "TODOS",
            vidaUtil: "TODOS",
            añoInstalacion: "TODOS",
            inversion: "TODOS",
            palabra: "",
          });
        } else {
          alertify.message("Intente de Nuevo");
        }
      }
    };

    const eliminarDepartamento = async (array) => {
      const idUsuario = localStorage.getItem("usuario");
      const idDestino = localStorage.getItem("idDestino");
      const action = "eliminarDepartamento";
      const url = `../apis/proyecciones.php?action=${action}&idDestino=${idDestino}&idUsuario=${idUsuario}&idDepartamento=${array.idDepartamento}`;

      const peticion = await fetch(url);
      const resp = await peticion.json();

      if (resp == 1) {
        alertify.success("Departamento Eliminado");
        store.dispatch("fetchItems", {
          idDestino: idDestino,
          idUsuario: idUsuario,
          ceco: "TODOS",
          departamento: "TODOS",
          vidaUtil: "TODOS",
          añoInstalacion: "TODOS",
          inversion: "TODOS",
          palabra: "",
        });
      } else {
        alertify.message("Intente de Nuevo");
      }
    };

    const editar = (array) => {
      departamento.value = array.departamento;
      seccion.value = array.seccion;
    };

    const editarDepartamento = async (array) => {
      try {
        const idUsuario = localStorage.getItem("usuario");
        const idDestino = localStorage.getItem("idDestino");

        const action = "editarDepartamento";
        const url = `../apis/proyecciones.php?action=${action}&idDestino=${idDestino}&idUsuario=${idUsuario}&idDepartamento=${array.idDepartamento}`;

        if (departamento.value.length <= 0) {
          departamento.value = array.departamento;
        }

        if (seccion.value.length <= 0) {
          seccion.value = array.seccion;
        }

        if (departamento.value.length <= 0 || seccion.value.length <= 0) {
          alertify.warning("Datos NO Validos, Intente de Nuevo");
          return;
        }

        if (
          departamento.value == array.departamento &&
          seccion.value == array.seccion
        ) {
          alertify.message("Sin Cambios");
          return;
        }

        const peticion = await fetch(url, {
          method: "POST",
          body: JSON.stringify({
            departamento: departamento.value,
            seccion: seccion.value,
          }),
        });
        const resp = await peticion.json();

        if (resp == 1) {
          alertify.success("Datos Actualizados");
          store.dispatch("fetchItems", {
            idDestino: idDestino,
            idUsuario: idUsuario,
            ceco: "TODOS",
            departamento: "TODOS",
            vidaUtil: "TODOS",
            añoInstalacion: "TODOS",
            inversion: "TODOS",
            palabra: "",
          });
        } else {
          alertify.message("Intente de Nuevo");
        }
      } catch (error) {
        console.log(error);
      }
    };

    const confirm = (opcion, array) => {
      const msj =
        opcion == "eliminarDepartamento"
          ? "ADVERTENCIA: Se Eliminará el Registro y sus Componentes"
          : opcion == "eliminarNivel2"
          ? "ADVERTENCIA: Se Eliminará el Registro y sus Componentes"
          : opcion == "agregarDepartamento"
          ? "INFORMACIÓN: Se agregara un departamento Vacio."
          : opcion == "agregarNivel2"
          ? "INFORMACIÓN: Se agregara Item Nivel 2."
          : opcion == "agregarNivel3"
          ? "INFORMACIÓN: Se agregara Item Nivel 2"
          : opcion == "editarDepartamento"
          ? "INFORMACIÓN: Se Actualizarán los Datos."
          : opcion == "editarNivel2"
          ? "INFORMACIÓN: Se Actualizarán los Datos."
          : "INFORMACIÓN: --";

      alertify.confirm(
        "¿Aplicar Acción?",
        msj,
        () => {
          // OPCION PARA AGREGAR NIVEL 3
          if (opcion == "agregarNivel3") {
            agregarNivel3(array);
            return;
          }

          // OPCION PARA AGREGAR NIVEL 2
          if (opcion == "agregarNivel2") {
            agregarNivel2(array);
            return;
          }

          // OPCION PARA ELIMINAR NIVEL 2
          if (opcion == "eliminarNivel2") {
            eliminarNivel2(array);
            return;
          }

          // OPCION PARA ELIMINAR DEPARTAMENTO
          if (opcion == "eliminarDepartamento") {
            eliminarDepartamento(array);
            return;
          }

          // OPCION PARA EDITAR DEPARTAMENTO
          if (opcion == "editarDepartamento") {
            editarDepartamento(array);
            return;
          }

          // OPCION PARA EDITAR NIVEL2
          if (opcion == "editarNivel2") {
            editarNivel2(array);
            return;
          }
        },
        () => {
          alertify.error("Proceso Cancelado");
        }
      );
    };

    return {
      confirm,
      edicion,
      departamento,
      seccion,
      editar,
      agregarNivel2,
    };
  },
};
</script>

<style scoped>
.tr {
  /* @apply bg-white; */
  border: 1px solid rgb(255, 255, 255);
  padding: 0.35em;
}

.td {
  padding: 1em;
}
</style>
