<template>
  <div class="w-full flex flex-col">
    <div
      class="w-full flex flex-row items-center justify-center"
      v-for="x in arrayInfo"
      :key="x"
    >
      <div class="w-1/2">
        <h1 class="font-semibold text-3xl uppercase">
          {{ x.ubicacion }}
          <button
            class="
              text-black
              hover:bg-black
              hover:text-white
              rounded-full
              w-8
              h-8
            "
            @click="confirmEditarDestino(x)"
          >
            <svg
              class="mx-auto w-4 flex-none"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
              ></path>
            </svg>
          </button>
        </h1>
      </div>
      <div
        class="flex flex-col flex-wrap justify-center w-1/2 items-center py-6"
      >
        <div class="flex flex-row py-4 justify-around">
          <datos-2 texto="Habitaciones" :valor="x.habitaciones"></datos-2>
          <datos-2
            texto="Edad Hotel"
            :valor="x.edadHotel > 0 ? x.edadHotel + ' años' : ''"
          ></datos-2>
          <datos-2 texto="Propietario" :valor="x.propietario"></datos-2>
          <datos-2 texto="Región" :valor="x.region"></datos-2>
        </div>
        <div class="flex flex-row py-4 justify-around">
          <datos-2 texto="Fecha de Apertura" :valor="x.fechaApertura"></datos-2>
          <datos-2
            texto="Fecha de expansión"
            :valor="x.fechaExpansion"
          ></datos-2>
          <datos-2
            texto="Expansión de habitaciones"
            :valor="x.expansionHabitaciones"
          ></datos-2>
        </div>
      </div>
    </div>
    <div></div>
    <div class="w-full flex flex-row justify-between items-end py-4">
      <selector
        :valores="arrayFiltros.departamento"
        texto="DEPARTAMENTO"
        @change="
          departamento = $event.target.value;
          filtrarArrayItems();
        "
      >
      </selector>
      <selector
        :valores="arrayFiltros.ceco"
        texto="CECO"
        @change="
          ceco = $event.target.value;
          filtrarArrayItems();
        "
      >
      </selector>
      <selector
        :valores="arrayFiltros.vidaUtil"
        texto="VIDA UTIL"
        @change="
          vidaUtil = $event.target.value;
          filtrarArrayItems();
        "
      >
      </selector>
      <selector
        :valores="arrayFiltros.añoInstalacion"
        texto="AÑO INSTALACIÓN"
        @change="
          añoInstalacion = $event.target.value;
          filtrarArrayItems();
        "
      >
      </selector>
      <selector
        :valores="arrayFiltros.inversion"
        texto="INVERSIÓN"
        @change="
          inversion = $event.target.value;
          filtrarArrayItems();
        "
      >
      </selector>
      <div class="w-1/4 flex items-center justify-center">
        <input
          type="search"
          name="buscador"
          placeholder="Buscar..."
          class="text-xs p-2 rounded-l-lg w-8/12 border"
          v-model="palabra"
        />
        <button
          class="
            p-2
            bg-gray-700
            text-gray-100
            font-semibold
            rounded-r-lg
            text-xs
          "
          @click="filtrarArrayItems"
        >
          <svg
            class="w-3"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
            ></path>
          </svg>
        </button>
      </div>
      <div class="flex">
        <button
          class="
            w-8
            h-8
            text-green-600
            bg-green-300
            hover:text-green-100
            rounded-full
            mr-4
            uppercase
            text-xs
            font-semibold
            flex
            justify-center
            items-center
          "
          @click="exportarExcel"
        >
          <svg
            class="w-6 h-6"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M15 13l-3 3m0 0l-3-3m3 3V8m0 13a9 9 0 110-18 9 9 0 010 18z"
            ></path>
          </svg>
        </button>
        <button
          v-if="xx === false"
          class="
            p-2
            bg-purple-200
            text-purple-600
            rounded-lg
            text-xs
            font-semibold
          "
          @click="xx = !xx"
        >
          Proyección
        </button>
        <button
          v-if="xx"
          class="
            p-2
            bg-green-200
            text-green-600
            rounded-lg
            text-xs
            font-semibold
          "
          @click="xx = !xx"
        >
          FF&E
        </button>
      </div>
    </div>
    <div class="w-full overflow-x-auto">
      <tablas v-if="xx === false"></tablas>
      <proyeccion v-if="xx"></proyeccion>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import { useStore } from "vuex";
import Proyeccion from "../components/Proyeccion.vue";
import Selector from "../components/Selector.vue";
import Tablas from "../components/tablas.vue";
import Datos2 from "../components/Datos2.vue";

export default {
  name: "Resumen",
  components: { Tablas, Selector, Proyeccion, Datos2 },
  setup() {
    // LIBRERIAS IMPORTADAS
    const store = useStore();
    const Swal = require("sweetalert2");
    const xx = ref(false);
    const palabra = ref("");
    const ceco = ref("TODOS");
    const departamento = ref("TODOS");
    const vidaUtil = ref("TODOS");
    const añoInstalacion = ref("TODOS");
    const inversion = ref("TODOS");

    onMounted(() => {
      store.dispatch("fetchInfo", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
      });
    });

    const arrayInfo = computed(() => {
      return store.state.arrayInfo;
    });

    const arrayFiltros = computed(() => {
      return store.state.arrayFiltros;
    });

    const filtrarArrayItems = () => {
      store.dispatch("fetchItems", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        ceco: ceco.value,
        departamento: departamento.value,
        vidaUtil: vidaUtil.value,
        añoInstalacion: añoInstalacion.value,
        inversion: inversion.value,
        palabra: palabra.value,
      });

      store.dispatch("getProyecciones", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        ceco: ceco.value,
        departamento: departamento.value,
        vidaUtil: vidaUtil.value,
        añoInstalacion: añoInstalacion.value,
        inversion: inversion.value,
        palabra: palabra.value,
      });
    };

    const exportarExcel = () => {
      location.href = `../php/exportar_excel_GET.php?action=reporteProyecciones&idDestino=${localStorage.getItem(
        "idDestino"
      )}&idUsuario=${localStorage.getItem("usuario")}&ceco=${
        ceco.value
      }&departamento=${departamento.value}&vidaUtil=${
        vidaUtil.value
      }&añoInstalacion=${añoInstalacion.value}&inversion=${
        inversion.value
      }&palabra=`;
    };

    const confirmEditarDestino = async (array) => {
      const { value: formValues } = await Swal.fire({
        title: "¿ACTUALIZAR DESTINO?",
        showCancelButton: true,
        confirmButtonText: "Actualizar",
        cancelButtonText: "Cancelar",
        html: `
        <div class="grid grid-cols-2 gap-6">
          
          <div class="flex flex-col justify-center items-center">
            <label class="font-bold text-lg pb-2">Habitaciones: </label>
            <input type="number" id="swal-input1" class="focus ring-2 ring-gray-400 p-2" value="${array.habitaciones}">
          </div>
          
          <div class="flex flex-col justify-center items-center">
            <label class="font-bold text-lg pb-2">Edad Hotel: </label>
            <input type="number" id="swal-input2" class="focus ring-2 ring-gray-400 p-2" value="${array.edadHotel}">
          </div>
          
          <div class="flex flex-col justify-center items-center">
            <label class="font-bold text-lg pb-2">Propietario: </label>
            <input type="text" id="swal-input3" class="focus ring-2 ring-gray-400 p-2" value="${array.propietario}">
          </div>
          
          <div class="flex flex-col justify-center items-center">
            <label class="font-bold text-lg pb-2">Región: </label>
            <input type="text" id="swal-input4" class="focus ring-2 ring-gray-400 p-2" value="${array.region}">
          </div>
          
          <div class="flex flex-col justify-center items-center">
            <label class="font-bold text-lg pb-2">Fecha Apertura: </label>
            <input type="date" id="swal-input5" class="focus ring-2 ring-gray-400 p-2" value="${array.fechaApertura}">
          </div>
          
          <div class="flex flex-col justify-center items-center">
            <label class="font-bold text-lg pb-2">Fecha Expansión: </label>
            <input type="date" id="swal-input6" class="focus ring-2 ring-gray-400 p-2" value="${array.fechaExpansion}">
          </div>
          
          <div class="flex flex-col justify-center items-center">
            <label class="font-bold text-lg pb-2">Expansión de Haitaciones: </label>
            <input type="text" id="swal-input7" class="focus ring-2 ring-gray-400 p-2" value="${array.expansionHabitaciones}">
          </div>

        </div>
      `,
        focusConfirm: false,
        preConfirm: () => {
          return [
            document.getElementById("swal-input1").value,
            document.getElementById("swal-input2").value,
            document.getElementById("swal-input3").value,
            document.getElementById("swal-input4").value,
            document.getElementById("swal-input5").value,
            document.getElementById("swal-input6").value,
            document.getElementById("swal-input7").value,
          ];
        },
      });

      if (formValues) {
        Swal.fire({
          title: "¿Desea actualizar los Datos?",
          showCancelButton: true,
          confirmButtonText: `Confirmar`,
        }).then((result) => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            if (
              formValues[0] >= 0 &&
              formValues[1].length >= 0 &&
              formValues[2].length >= 0 &&
              formValues[3].length >= 0 &&
              formValues[4].length >= 0 &&
              formValues[5].length >= 0 &&
              formValues[6].length >= 0
            ) {
              editarDestino({
                habitaciones: formValues[0],
                edadHotel: formValues[1],
                propietario: formValues[2],
                region: formValues[3],
                fechaApertura: formValues[4],
                fechaExpansion: formValues[5],
                expansionHabitaciones: formValues[6],
              });
            }
          } else if (result.isDismissed) {
            Swal.fire("Cambios Descartados", "", "info");
          }
        });
      }
    };

    const editarDestino = async (array) => {
      try {
        const idUsuario = localStorage.getItem("usuario");
        const idDestino = localStorage.getItem("idDestino");

        const action = "editarDestino";
        const url = `../apis/proyecciones.php?action=${action}&idDestino=${idDestino}&idUsuario=${idUsuario}&idItem=${array.idItem}`;
        const peticion = await fetch(url, {
          method: "POST",
          body: JSON.stringify({
            habitaciones: array.habitaciones,
            edadHotel: array.edadHotel,
            propietario: array.propietario,
            region: array.region,
            fechaApertura: array.fechaApertura,
            fechaExpansion: array.fechaExpansion,
            expansionHabitaciones: array.expansionHabitaciones,
          }),
        });
        const resp = await peticion.json();

        if (resp == 1) {
          Swal.fire("Datos Actualizados!", "", "success");
          store.dispatch("fetchInfo", {
            idDestino: idUsuario,
            idUsuario: idDestino,
          });
        } else {
          alertify.message("Intente de Nuevo");
        }
      } catch (error) {
        console.log(error);
      }
    };

    return {
      xx,
      arrayInfo,
      arrayFiltros,
      palabra,
      filtrarArrayItems,
      ceco,
      departamento,
      vidaUtil,
      añoInstalacion,
      inversion,
      exportarExcel,
      confirmEditarDestino,
    };
  },
};
</script>
