<template>
  <div class="px-2 pt-4 pb-8 w-full">
    <table class="w-full text-xs table-fixed">
      <tr class="cursor-pointer">
        <th class="w-1/10">MARCA</th>
        <th class="w-1/10">NODO</th>
        <th class="w-1/10">DEPTO/UBICACIÓN</th>
        <th class="w-1/10">VIDA ÚTIL</th>
        <th class="w-1/10">AÑO INSTALACIÓN</th>
        <th class="w-1/10">INVERSIÓN</th>
        <th class="w-1/10">COSTE</th>
        <th class="w-1/10">UNIDADES</th>
        <th class="w-1/10">TOTAL</th>
        <th class="w-1/10">
          <button
            class="
              p-2
              bg-blue-200
              rounded-lg
              my-2
              text-xxs
              uppercase
              font-semibold
            "
            @click="agregarDepartamento"
          >
            Añadir depto
          </button>
        </th>
      </tr>

      <TablaNivel2
        v-for="(x, y) in array"
        :key="y"
        :array="x"
        :toggle1="toggle1"
      />
    </table>
  </div>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import { useStore } from "vuex";
import TablaNivel2 from "../components/TablaNivel2.vue";

export default {
  name: "TablaNivel1",
  components: { TablaNivel2 },
  props: { array: Object },

  setup() {
    const store = useStore();
    const toggle1 = ref(true);
    const Swal = require("sweetalert2");

    // PROPIEDAD GLOBAL ALERTIFY JS PARA POSICIÓN DEL ALERT
    alertify.set("notifier", "position", "top-right");
    alertify.defaults.transition = "zoom";
    alertify.defaults.theme.ok = "ui positive button";
    alertify.defaults.theme.cancel = "ui black button";

    onMounted(async () => {
      await store.dispatch("fetchItems", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        ceco: "TODOS",
        departamento: "TODOS",
        vidaUtil: "TODOS",
        añoInstalacion: "TODOS",
        inversion: "TODOS",
        palabra: "",
      });
      await store.dispatch("getFiltros");
    });

    const arrayItems = computed(() => {
      return store.state.arrayItems;
    });

    const confirmAgregarDepartamento = () => {
      alertify.confirm(
        "¿Aplicar Acción?",
        "INFORMACIÓN: Se agregara un Departamento",
        () => {
          agregarDepartamento();
        },
        () => {
          alertify.error("Proceso Cancelado");
        }
      );
    };

    const agregarDepartamento = async (array) => {
      const { value: formValues } = await Swal.fire({
        title: "¿AGREGAR DEPARTAMENTO?",
        showCancelButton: true,
        html: `
        <div class="form flex flex-col justify-center items-center">
          <label class="font-bold text-xl">Departamento: </label>
          <input id="swal-input1" class="swal2-input">
        </div>

        <div class="mt-10 form flex flex-col justify-center items-center">
          <label class="font-bold text-xl">Ceco/Sección: </label>
          <input id="swal-input2" class="swal2-input">
        </div>
      `,
        focusConfirm: false,
        preConfirm: () => {
          return [
            document.getElementById("swal-input1").value,
            document.getElementById("swal-input2").value,
          ];
        },
      });

      if (formValues) {
        if (formValues[0].length <= 0 && formValues[1].length <= 0) {
          alertify.message("Datos NO Validos");
          return;
        }

        const idUsuario = localStorage.getItem("usuario");
        const idDestino = localStorage.getItem("idDestino");
        const action = "agregarDepartamento";
        const url = `../apis/proyecciones.php?action=${action}&idDestino=${idDestino}&idUsuario=${idUsuario}`;

        const peticion = await fetch(url, {
          method: "POST",
          body: JSON.stringify({
            departamento: formValues[0],
            seccion: formValues[1],
          }),
        });
        const resp = await peticion.json();
        if (resp == 1) {
          alertify.success("Departamento Agregado");
          store.dispatch("fetchItems", {
            idDestino: idDestino,
            idUsuario: idUsuario,
            ceco: "TODOS",
            departamento: "TODOS",
            vidaUtil: "TODOS",
            añoInstalacion: "TODOS",
            inversion: "TODOS",
            palabra: "",
          });
        } else {
          alertify.message("Intente de Nuevo");
        }
      }
    };

    return {
      arrayItems,
      toggle1,
      confirmAgregarDepartamento,
      agregarDepartamento,
    };
  },
};
</script>

<style scope>
.positive {
  color: white;
  font-size: 1.2rem;
  /* background-color: #5bbd72 !important; */
  background-color: rgba(52, 211, 153);
  border-radius: 0.45rem;
}

.black {
  color: white;
  font-size: 1.2rem;
  border-radius: 0.45rem;
  /* background-color: #5c6166; */
  background-color: rgba(55, 65, 81);
}
</style>