<template>
  <tr v-for="(e, f) in array" :key="f" class="">
    <td colspan="10">
      <table class="w-full table-fixed">
        <tr
          class="cursor-pointer tr"
          v-if="toggle1 && toggle2 && toggle3 && toggle4"
        >
          <td class="w-1/10 h-12 bg-green-500" data-label="CECO td"></td>
          <td class="w-1/10 bg-green-500" data-label="CECO td">ACTIVO</td>
          <td
            class="
              w-1/10
              bg-green-500
              border-r-2 border-l-2 border-green-100
              td
            "
            data-label="TITULO"
          >
            <div v-if="edicion3">
              {{ e.titulo }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion3">
              <input
                class="
                  py-2
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                v-model="data.titulo3"
              />
            </div>
          </td>
          <td
            class="
              w-1/10
              bg-green-500
              border-r-2 border-l-2 border-green-100
              td
            "
            data-label="VIDA ÚTIL"
          >
            <div v-if="edicion3">
              {{
                e.vidaUtil == 1
                  ? e.vidaUtil + " año"
                  : e.vidaUtil > 1
                  ? e.vidaUtil + " años"
                  : 0 + " años"
              }}
            </div>
            <div
              class="py-0 max-w-xs w-full mx-auto flex flex-row items-center"
              v-if="!edicion3"
            >
              <input
                class="
                  py-2
                  px-2
                  mx-3
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Años Vida Util"
                v-model="data.vidaUtil"
              />
              años
            </div>
          </td>
          <td
            class="
              w-1/10
              bg-green-500
              border-r-2 border-l-2 border-green-100
              td
            "
            data-label="AÑO INSTALACIÓN"
          >
            <div v-if="edicion3">
              {{ e.añoInstalacion }}
            </div>
            <div class="max-w-xs w-full mx-auto" v-if="!edicion3">
              <select
                class="font-bold text-lg text-green-500"
                v-model="data.añoInstalacion"
                :value="e.añoInstalacion"
              >
                <option disabled>Seleccione</option>
                <option v-for="year in 31" :key="year" :value="2000 + year">
                  {{ 2000 + year }}
                </option>
              </select>
            </div>
          </td>
          <td
            class="
              w-1/10
              bg-green-500
              border-r-2 border-l-2 border-green-100
              td
            "
            data-label="INVERSIÓN"
          >
            <div v-if="edicion3">
              {{ e.inversion }}
            </div>
            <div
              class="
                py-0
                max-w-xs
                w-full
                mx-auto
                flex flex-row
                items-center
                justify-between
              "
              v-if="!edicion3"
            >
              <div>
                <input
                  class="form-radio"
                  type="radio"
                  :id="['opt_1_' + e.idItem]"
                  value="OS&E"
                  v-model="data.inversion"
                />
                <label
                  class="text-green-500 font-bold ml-1 text-sm"
                  :for="['opt_1_' + e.idItem]"
                  >OS&E</label
                >
              </div>
              <div>
                <input
                  type="radio"
                  :id="['opt_2_' + e.idItem]"
                  value="FF&E"
                  v-model="data.inversion"
                />
                <label
                  class="text-green-500 font-bold ml-1 text-sm"
                  :for="['opt_2_' + e.idItem]"
                  >FF&E</label
                >
              </div>
            </div>
          </td>
          <td
            class="
              w-1/10
              bg-green-500
              border-r-2 border-l-2 border-green-100
              td
            "
            data-label="COSTE"
          >
            <div v-if="edicion3">
              $ {{ new Intl.NumberFormat().format(e.coste) }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion3">
              <input
                class="
                  py-2
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                placeholder="Coste"
                v-model="data.coste"
              />
            </div>
          </td>
          <td
            class="
              w-1/10
              bg-green-500
              border-r-2 border-l-2 border-green-100
              td
            "
            data-label="UNIDADES"
          >
            <div v-if="edicion3">
              {{ e.unidades > 0 ? e.unidades : "-" }}
            </div>
            <div class="py-0 max-w-xs w-full mx-auto" v-if="!edicion3">
              <input
                class="
                  py-2
                  px-2
                  bg-white
                  rounded-lg
                  placeholder-gray-400
                  text-gray-900
                  appearance-none
                  inline-block
                  w-full
                  shadow-md
                  focus:outline-none
                  focus:ring-2 focus:ring-blue-600
                "
                :placeholder="e.unidades + ' Unidades'"
                v-model="data.unidades"
              />
            </div>
          </td>
          <td
            class="w-1/10 bg-green-500 border-l-2 border-green-100 td"
            data-label="TOTAL"
          >
            $ {{ e.total > 0 ? new Intl.NumberFormat().format(e.total) : 0 }}
          </td>
          <td class="w-1/10 bg-green-500 border-green-100 td">
            <div class="flex flex-row justify-evenly" v-if="edicion3">
              <div class="w-5 h-5"></div>

              <button
                class="
                  bg-yellow-200
                  py-2
                  px-2
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="
                  edicion3 = !edicion3;
                  editarItem(e);
                "
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-red-200
                  py-2
                  px-2
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="confirm('eliminarNivel3', e)"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                  ></path>
                </svg>
              </button>
            </div>

            <div class="flex flex-row justify-evenly" v-if="!edicion3">
              <div class="w-5 h-5"></div>

              <button
                class="
                  bg-green-400
                  py-1
                  px-2
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="
                  edicion3 = !edicion3;
                  confirm('editarNivel3', e);
                "
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                    clip-rule="evenodd"
                  ></path>
                </svg>
              </button>

              <button
                class="
                  bg-gray-400
                  px-2
                  py-1
                  rounded-full
                  w-5
                  h-5
                  flex
                  items-center
                  justify-center
                "
                @click="edicion3 = !edicion3"
              >
                <svg
                  class="mx-auto w-4 flex-none"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill-rule="evenodd"
                    d="M7.707 3.293a1 1 0 010 1.414L5.414 7H11a7 7 0 017 7v2a1 1 0 11-2 0v-2a5 5 0 00-5-5H5.414l2.293 2.293a1 1 0 11-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z"
                    clip-rule="evenodd"
                  ></path>
                </svg>
              </button>
            </div>
          </td>
        </tr>
      </table>
    </td>
  </tr>
</template>

<script>
import { useStore } from "vuex";
import { ref } from "vue";
export default {
  name: "Trnivel4",
  props: {
    array: Object,
    toggle1: Boolean,
    toggle2: Boolean,
    toggle3: Boolean,
    toggle4: Boolean,
  },
  setup() {
    const data = ref({
      titulo3: "",
      vidaUtil: "",
      añoInstalacion: "",
      inversion: "",
      coste: "",
      unidades: "",
      total: "",
    });
    const edicion3 = ref(true);
    return { edicion3, data };
  },
};
</script>

<style>
</style>